html {
  box-sizing: border-box;
  --main-color: #00acdc;
  --header-height: 60px;
  margin: 0;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  display: inline-block;
}

.logo {
  display: block;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list_item {
  margin: 0 0 0 20px;
}

.list_item:first-child {
  margin-left: 0;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  height: var(--header-height);
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
  display: inline-block;
}

.nav_link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  z-index: 1;
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}
